#blocker {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 3em 0;
  transition: 1s background-color;
  z-index: 0;
}

#loading {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 1.0);
  color: #fff;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 1em 0;
  transition: 1s opacity;
  z-index: 99;
  text-align: center;
}

#loading.fade-out {
  opacity: 0;
}

#loader {
  -webkit-animation: fade 2s linear infinite;
  animation: fade 2s linear infinite;
}

.load {
  width: 50%;
  height: 50%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  text-align: center;
  top: 45%;
  left: -1%;
}

.load span {
  font-size: 1.7vw;
}

@keyframes fade {
  0% {
    display: block;
    opacity: 1;
  }

  50% {
    display: none;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

@-webkit-keyframes fade {
  0% {
    display: block;
    opacity: 1;
  }

  50% {
    display: none;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

