.mouse-lockup {
  margin: 1vw 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mouse-arrow-left {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 10px 5px 0;
  border-color: transparent #ffffff transparent transparent;
}

.mouse-arrow-left:after {
  content: "";
  width: 10px;
  height: 2px;
  background-color: #fff;
  display: block;
  margin-top: -1px;
  margin-left: 6px;
}

.mouse-body {
  width: 2vw;
  border: 3px solid #fff;
