.mouse-wheel {
  background-color: #fff;
  width: 0.45vw;
  height: 16px;
  border-radius: 0.9vw;
  /* align-items: center; */
  position: relative;
  left: 40%;
}

.mouse-arrow-right {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 0 5px 10px;
  border-color: transparent transparent transparent #ffffff;
}

.mouse-arrow-right:after {
  content: "";
  width: 10px;
  height: 2px;
  background-color: #fff;
  display: block;
  margin-top: -1px;
  margin-left: -16px;
}

