/* PhotoGallery CSS for Tasneem */

.gallerybody_tasneem {
  height: 100%;
  width: 53%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  overflow: hidden;
}

.gallery_tasneem {
  display: grid;
  grid-template-rows: repeat(5, 15vh);
  grid-template-columns: repeat(2, 20vw);
  transition: all 0.3s cubic-bezier(0.5, 0.8, 0.5, 0.2);
  /* background-color: #82a6cb; */
  background-color: transparent;
  border-radius: 10px;
  padding: 0.25em;
  cursor: pointer;
}

@media (min-width: 40em) {
  .gallery_tasneem {
    grid-template-columns: repeat(6, 18vw);
    grid-template-rows: repeat(1, 15vw);
  }
}

.gallery_tasneem img {
  object-fit: fill;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: all 0.15s cubic-bezier(0.3, 0.1, 0.3, 0.85);
  position: relative;
  box-shadow: 0 0 0 #0000;
  opacity: 0.8;
  filter: sepia(80%) hue-rotate(180deg);
}

.gallery_tasneem img:first-child {
  border-radius: 10px 10px 0 0;
}

.gallery_tasneem img:last-child {
  border-radius: 0 0 10px 10px;
}

@media (min-width: 40em) {
  .gallery_tasneem img:first-child {
    border-radius: 10px 0 0 10px;
  }

  .gallery_tasneem img:nth-child(5) {
    border-radius: 0 0 0 0;
  }

  .gallery_tasneem img:nth-child(6) {
    border-radius: 0 0 0 0;
  }

  .gallery_tasneem img:last-child {
    border-radius: 0 10px 10px 0;
  }
}

.gallery_tasneem img:hover {
  opacity: 1;
  z-index: 1;
  box-shadow: 1em 1em 1em #0004;
  transition: all 0.3s cubic-bezier(0.3, 0.1, 0.3, 0.85);
  filter: sepia(0%) hue-rotate(0deg);
  border-radius: 5px;
  width: 125%;
  height: 125%;
  left: -25%;
  top: -10%;
}

@media (min-width: 15em) and (max-width: 40em) and (orientation: portrait) {
  .gallery_tasneem {
    display: grid;
    grid-template-rows: repeat(1, 18vw);
    grid-template-columns: repeat(6, 11vw);
  }

  .gallery_tasneem img:first-child {
    border-radius: 10px 0 0 10px;
  }

  .gallery_tasneem img:nth-child(5) {
    border-radius: 0 0 0 0;
  }

  .gallery_tasneem img:nth-child(6) {
    border-radius: 0 0 0 0;
  }

  .gallery_tasneem img:last-child {
    border-radius: 0 10px 10px 0;
  }

  .gallery_tasneem img:hover {
    width: 150%;
    height: 150%;
    left: -40%;
    top: -25%;
    border-radius: 5px 0px 0px 5px;
  }
}

@media (min-width: 40em) and (max-width:59.9375em) {
  .gallery_tasneem {
    display: grid;
    grid-template-rows: repeat(1, 14vw);
    grid-template-columns: repeat(6, 16%);
  }
}

@media (min-width: 40em) and (orientation: landscape) {
  .gallery_tasneem img:hover {
    width: 125%;
    height: 125%;
    left: -5%;
    top: -15%;
  }

  .gallery_tasneem img:hover~img {
    left: 20%;
  }
}

@media (min-width: 60em) {
  .gallery_tasneem {
    grid-template-columns: repeat(6, 16%);
    grid-template-rows: 14vw;
    place-content: center;
  }

  .gallery_tasneem img:hover {
    width: 125%;
    height: 125%;
    left: -5%;
    top: -15%;
  }
}

@media (min-width: 70em) {
  .gallery_tasneem {
    place-content: center;
  }
}

@media (min-width: 73em) {
  .gallery_tasneem {
    place-content: center;
  }
}

@media (min-width: 78.125em) {
  .gallery_tasneem {
    grid-template-columns: repeat(6, 15%);
    place-content: center;
  }
}

@media (min-width: 87.375em) {
  .gallery_tasneem {
    place-content: center;
  }
}

@media (min-width: 93.5em) {
  .gallery_tasneem {
    place-content: center;
  }
}

@media (min-width: 102.5em) {
  .gallery_tasneem {
    place-content: center;
  }
}

